/* Homepage */
.homepage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
}
/* Homepage Product Card */
.homepage .product-card {
    flex: 1 1 calc(33.333% - 20px);
    margin: 10px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
    transition: transform 0.3s ease;
}
/* Homepage Product Card Hover Effect */
.homepage .product-card:hover {
    transform: scale(1.05);
}
/* Homepage Product Card Image */
.homepage .product-card img {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: cover;
    border-radius: 5px;
}
/* Homepage Product Card Title */
.homepage .product-card h3 {
    margin: 10px 0;
    font-size: 1.5rem;
    color: #333;
}
/* Homepage Product Card Description */
.homepage .product-card p {
    margin: 10px 0;
    font-size: 1rem;
    color: #666;
}
/* Homepage Product Card Price */
.homepage .product-card .price {
    font-size: 1.2rem;
    color: #28a745;
    margin: 10px 0;
}
/* Homepage Product Card Button */
.homepage .product-card a {
    display: inline-block;
    padding: 10px 15px;
    text-decoration: none;
    background-color: #007bff;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    margin-top: 10px;
    font-size: 0.9rem;
}
/* Homepage Product Card Button Hover Effect */
.homepage .product-card a:hover {
    background-color: #0056b3;
}
