/* main.css */

/* General Styles */
body {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    color: #333;
  }
  
  .container {
    max-width: 1200px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Navbar */
  .navbar {
    background-color: #333;
    color: #fff;
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar h2 {
    margin: 0;
  }
  
  .navbar div a {
    color: #fff;
    text-decoration: none;
    margin-left: 20px;
  }
  
  .navbar div a:hover {
    text-decoration: underline;
  }
  
  /* Product Grid (Home Page) */
  .product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .product-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .product-card img {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: cover;
  }
  
  .product-card h3 {
    margin: 10px;
    font-size: 1.2rem;
  }
  
  .product-card p {
    margin: 5px 10px;
    font-size: 0.9rem;
    color: #666;
  }
  
  .product-card a {
    display: inline-block;
    padding: 10px 15px;
    text-decoration: none;
    background-color: #007bff;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    margin: 10px;
    font-size: 0.9rem;
  }
  
  .product-card a:hover {
    background-color: #0056b3;
  }
  
  .product-card button {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px 10px 10px;
    font-size: 0.9rem;
  }
  
  .product-card button:hover {
    background-color: #1e7e34;
  }
  
  /* Product Detail Page */
  .product-detail {
    display: flex;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    max-width: 800px;
    margin: 20px auto;
  }
  
  .product-detail img {
    max-width: 300px;
    height: auto;
    margin-right: 20px;
    border-radius: 5px;
  }
  
  .product-detail div {
    flex-grow: 1;
  }
  
  .product-detail h2 {
    font-size: 1.8rem;
    margin-top: 0;
  }
  
  .product-detail p {
    line-height: 1.6;
    color: #555;
    margin-bottom: 15px;
  }
  
  .product-detail button {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 12px 20px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .product-detail button:hover {
    background-color: #1e7e34;
  }
  
  /* Cart Page */
  .cart-page {
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    max-width: 800px;
    margin: 20px auto;
  }
  
  .cart-page h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .cart-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #eee;
    padding: 15px 0;
  }
  
  .cart-item:last-child {
    border-bottom: none;
  }
  
  .cart-item img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 15px;
    border-radius: 5px;
  }
  
  .cart-item h4 {
    font-size: 1rem;
    margin-top: 0;
    margin-bottom: 5px;
  }
  
  .cart-item p {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 5px;
  }
  
  .cart-item button {
    background-color: #dc3545;
    color: #fff;
    border: none;
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 0.8rem;
  }
  
  .cart-item button:hover {
    background-color: #c82333;
  }
  
  .cart-page p {
    font-style: italic;
    color: #777;
  }
  
  /* Auth Pages (Login & Register) */
  .auth-page {
    padding: 30px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    max-width: 400px;
    margin: 50px auto;
  }
  
  .auth-page h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .auth-page form {
    display: flex;
    flex-direction: column;
  }
  
  .auth-page input {
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .auth-page button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 12px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .auth-page button:hover {
    background-color: #0056b3;
  }
  
  /* Responsive Design (Basic Example) */
  @media (max-width: 768px) {
    .product-grid {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  
    .product-detail {
      flex-direction: column;
      align-items: center;
    }
  
    .product-detail img {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  
    .cart-item {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .cart-item img {
      margin-bottom: 10px;
    }
  }